@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --white: #fff;
  --black: #000;
}

::-webkit-input-placeholder {
  /* Edge */
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

::placeholder {
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

body {
  font-family: "Open Sans", sans-serif;
  background: #fafafa;
}
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 54px;
  background: #fff;
  background: var(--white);
  display: -webkit-flex;
  display: flex;
  padding:0 150px;
  border-bottom: 1px solid #efefef;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content:space-evenly;
          justify-content:space-evenly;
}

.navbar__first {
  width: 33.33333333333333%;
}
.navbar_first_logo {
  margin-top: 6px;
}
.navbar_first_logo img {
  object-fit: cover;
  display: block;
  width: 100px;
  font-weight: bold;
  cursor: pointer;
}
.navbar_middle {
  width: 33.33333333333333%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-left: 80px;
}
.navbar_middle_search {
  position: relative;
}
.navbar_search {
  background: #fafafa;
  border: 1px solid #d2dbdb;
  padding: 7px 45px;
  border-radius: 3px;
  outline: none;
  font-size: 14px;
  font-weight: 400;
}
.searchIcon {
  position: absolute;
  top: 10px;
  left: 30px;
  color: silver;
  font-size: 14px;
}
.navbar__last {
  width: 33.33333333333333%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.navbar__last li {
  list-style-type: none;
  display: inline-block;
  margin: 0 8px;
  cursor: pointer;
}
.navbar__icons {
  font-size: 25px;
  cursor: pointer;
}
.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .model_container {
    width: 400px;
    height: auto;
    padding:2px 15px;
    border-radius: 4px;
    background: rgb(255, 255, 255);
  }
  .model_group{
    margin:10px 0;
  }
  .model_group img{
      width: 100px;
      margin:0 100px;
  }
  .model_input {
    width: 100%;
    height: 40px;
    padding: 0 12px;
    border: 1px solid #d2dbdb;
    outline: none;
    border-radius: 3px;
  }
  .btn {
    outline: none;
    border: none;
    color: #fff;
    height: 40px;
    padding: 0 13px;
    border-radius: 3px;
    cursor: pointer;
  }
  .btn_smart {
    width: 100%;
    background: #f83a63;
  }
  .model_group span{
    font-size: 13px;
    cursor: pointer;
  }
  .container{
    width: 100%;
    padding: 0 200px;
    margin-top: 80px;
  }
  .stories{
    border-radius: 3px;
    border: 1px solid #efefef;
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
    padding: 15px;
    width: 65%;
    overflow: hidden;
  }
  .stories_info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 6px;
    cursor: pointer;
  }
  .stories_img {
    width: 66px;
    height: 66px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid #f83a63;
    padding: 3px;
  }
  .stories_img span {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
  }
  .stories_img span img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .stories_name {
    font-size: 11px;
    margin: 2px 12px;
  }
  .create{
    width: 65%;
    margin: 15px 0;
    background-color:  #fff;
    padding: 10px;
    border: 1px solid #efefef;
    border-radius: 3px;
  }
  .create_inputt{
    width: 100%;
    height: 40px;
    padding: 0 13px;
    border: 1px solid #efefef;
    outline: none;
    border-radius: 2px;
  }
  .create_second{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10px;
  }
  .file{
    display: none;
  }
  .camera{
    color: #f83a63;
    cursor: pointer;
    font-size: 30px;
  }
  .create_second-a{
    -webkit-flex: 1 1;
            flex: 1 1;
  }
  .create_second-b{
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: right;
  }
  .btn-sweet{
    background-color: #f83a63;
    color: #fff;
    text-decoration: none;
    border: none;
    padding: 6px 10px;
    border-radius: 2px;
    cursor: pointer;
  }
  .posts{
    width: 65%;
    background-color: #fff;
    margin: 15px 0;
  }
  .posts_header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background: #fff;
    padding: 10px;
  }
  .posts_header_avator{
    width: 40px;
    height: 40px;
    border-radius: 50px;
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background: #f83a63;
    text-transform: uppercase;
  }
  .posts_header-name{
    margin-left: 8px;
    text-transform: capitalize;
  }
  .posta_img{
    width: 100%;
    overflow: hidden;
  }
  .posta_img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .comments{
    margin-top: 10px;
  }
  .comment_input {
    width: 100%;
    height: 40px;
    border-top: 1px solid #efefef;
    border-bottom: 1px solid #efefef;
    border-left: none;
    border-right: none;
    padding: 0 15px;
    outline: none;
  }
  .comments_container{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 10px 10px 10px;
  }
  .comments_container-name{
    margin-right: 8px;
    text-transform: capitalize;
  }
  .comments_container-msg{
    font-size: 14px;
    color: silver;
    font-weight: 300;
  }
  .sidebar{
      position: fixed;
      top: 100px;
      right: 200px;
      width: 25%;
      padding-left: 25px;
  }
  .sidebar_user{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  .sidebar_user-avtor{
    width: 60px;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 18px;
    color:  #fff;
    background-color: #f83a63;
    border-radius: 50%;
    text-transform: uppercase;
  }
  .sidebar_user-name{
    padding-left: 10px;
    text-transform: capitalize;
  }
  .sidebar_list{
    margin: 20px 0;
  }
  .sidebar_list h3{
    font-size: 15px;
    font-weight: 400;
    color: silver;
  }
  .sidebar_list-user{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10px;
  }
  .sidebar_list-a{
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  .sidebar_list-a-img{
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 50%;    
  }
  .sidebar_list-a-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .sidevar_list-a-name{
    margin-left: 8px;
    font-size: 14px;
  }
  .sidebar_list-b{
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: right;
  }
  .sidebar_list-b a{
    text-decoration: none;
    color: #098bbe;
    font-size: 14px;
  }
  @media(max-width: 765px){
    .navbar{
      padding: 0 20px;
    }
    .navbar_middle{
      display: none;
    }
    .navbar__icons{
      display: none;
    }
    .sidebar{
      display: none;
    }
    .container{
      padding: 0 20px;
    }
    .stories{
      width: 90%;
      margin: 0 auto;
    }
    .create{
      width: 90%;
      margin: 15px auto;
    }
    .posts{
      width: 90%;
      margin: 15px auto;
    }
    /* .post-container{
      width: 90%;
      margin: 0 ;
      align-items: center;
      justify-content: center;
    } */
  }
